* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root {
  --fixed-width: 620px;
}
nav {
  width: 100%;
  padding: 10px;
  padding-left: 30px;
}
.side-bar-btn {
  font-size: 28px;
  color: tomato;
  padding: 1px;
  border-color: transparent;
  background-color: inherit;
  animation: scaleani 3s infinite;
  outline-color: transparent;
  top: 10px;
  left: 35px;
  position: fixed;
}
.side-bar-btn:hover {
  color: rgb(236, 57, 26);
}
@keyframes scaleani {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

.homecontainer {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(219, 250, 248);
}
.btn {
  display: inline-block;
  border-color: transparent;
  padding: 5px;
  font-size: 18px;
  color: white;
  text-transform: capitalize;
  background-color: rgb(51, 48, 48);
  border-radius: 5px;
  padding-left: 15px;
  padding-right: 15px;
}
.btn-2 {
  padding: 5px;
  font-size: 25px;
  color: tomato;
  background-color: inherit;
  border-color: transparent;
  line-height: 1px;
  outline-color: transparent;
}
.btn:hover {
  background-color: rgb(22, 21, 21);
}
.modal-container {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 99;
  background-color: rgba(44, 43, 43, 0.336);
  top: 0;
  left: 0;
  display: grid;
  justify-content: center;
  align-items: center;
  padding: 10px;
  visibility: hidden;
  transform: scale(0);
  transition: all 0.3s linear;
}
.active {
  z-index: 99;
  visibility: visible;
  transform: scale(1);
}
.content {
  padding: 15px;
  width: 90vw;
  height: 30vh;
  max-width: 650px;
  /* max-width: var(--fixed-width); */
  background-color: white;
  box-shadow: 5px 5px 10px gray;
  display: grid;
  place-items: center;
  border-radius: 5px;
  position: relative;
}

.content h1 {
  font-size: 23px;
  color: rgb(36, 36, 36);
}
.close {
  position: absolute;
  top: 5px;
  right: 10px;
}
.sidebar-container {
  width: 400px;
  height: 100vh;
  background-color: white;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: 0.5s ease-in-out;
  /* display: grid; */
  /* place-items: center; */
}
.side-bar-active {
  transform: translateX(0);
}
.logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 10px;
}
.main-links-container {
  margin-top: 40px;
}
.main-links {
  list-style-type: none;
}
.main-links li {
  width: 100%;
}
.main-links li a {
  width: 100%;
  text-decoration: none;
  color: rgb(92, 92, 92);
  padding: 10px;
  padding-left: 30px;
  font-size: 24px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
}
.main-links li a:hover {
  background-color: rgb(219, 250, 248);
}
.icon {
  margin-right: 15px;
}

.social-links {
  width: 80%;
  margin: auto;
  text-align: center;
  position: absolute;
  bottom: 30px;
  /* height: 30px; */
  left: 50px;
}
.social-links a {
  font-size: 24px;
  text-decoration: none;
  color: rgb(41, 134, 134);
  margin: 5px;
}
.social-links a:hover {
  color: rgb(150, 226, 226);
}

@media only screen and (max-width: 800px) {
  .sidebar-container {
    width: 100vw;
    height: 100vh;
  }
}
